body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
}

.container{
    position:relative;
    z-index:0;
}

.video {
    position:relative;
    top: 0;
    left: 0;
    z-index:0;
}

.overlay {
    position:absolute;
    top: 400px;
    left: 500px;
    z-index:1;
}

.parent{
    position:relative;
}

.child{
    position:absolute;
    top: 400px;
    left:300px;
    /* margin: auto; */
}

.childanother{
    position:absolute;
    top: 440px;
    left:300px;
}

.marginAutoContainer {
    width: "100%";
    /* height: "auto"; */
    /*display: 'flex';*/
    position: relative;
  }

  .marginAutoItem {
      margin-top: 400px;
      margin-bottom: 0;
      position: absolute;
      margin-left: auto;
      margin-right: auto;
      display: block;
      width: 400px;
    }
